
import { Component, Input, Output, EventEmitter, OnInit, OnChanges } from '@angular/core';

import { BaseComponent } from '../../../shared/globals/base.component';
import { AppService } from '../../../services/app.service';

import * as faker from 'faker';
import * as moment from 'moment';

@Component({
    selector: 'locker-listings',
    templateUrl: './locker-listings.template.html',
    styleUrls: ['./locker-listings.styles.scss']
})
export class LockerListingsComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public date: number;
    @Input() public count: number;
    @Output() public countChange = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService) {
        super();
        this.model.list = [];
        this.postCount();
    }

    public ngOnInit() {
        this.subscription('lockers', this.service.Users.listen('user', (user) => {
            if (user) {
                this.model.list = this.service.Users.current().lockers;
                this.updateList();
            }
        }));
    }

    public ngOnChanges(changes: any) {
        if (changes.date) {
            this.model.date_display = moment(this.date).format('DD MMM YYYY');
            this.updateList();
        }
    }

    public view(item) {
        this.service.Visitors.view(item);
    }

    public updateList() {
        const bld = this.service.Buildings.current();
        if (!bld) {
            return this.timeout('update', () => this.updateList());
        }
        const now = moment();
        for (const item of (this.model.list || [])) {
            const expiry = moment(item.expiry * 1000);
            item.expired = now.isAfter(expiry);
            const remaining = moment.duration(expiry.diff(now)).asDays();
            item.display = {
                expiry: expiry.format('DD MMM YYYY'),
                duration: remaining < 0 ? 'Expired' : Math.round(remaining)
            };
            for (const lvl in bld.lockers) {
                if (bld.lockers.hasOwnProperty(lvl)) {
                    for (const section in bld.lockers[lvl]) {
                        if (bld.lockers[lvl].hasOwnProperty(section)) {
                            const section_data = bld.lockers[lvl][section];
                            for (const type in section_data) {
                                if (section_data.hasOwnProperty(type)) {
                                    for (const row of section_data[type]) {
                                        if (row.indexOf(item.id) >= 0) {
                                            item.level = this.service.Buildings.getLevel(lvl).name;
                                            item.section = section;
                                            break;
                                        }
                                    }
                                    if (item.level) { break; }
                                }
                            }
                            if (item.level) { break; }
                        }
                    }
                    if (item.level) { break; }
                }
            }
        }
        this.postCount();
    }

    public postCount() {
        setTimeout(() => {
            this.count = this.model.list ? this.model.list.length : 0;
            this.countChange.emit(this.count);
        }, 50);
    }

    public open(locker) {
        locker.opening = true;
        this.service.Lockers.open(locker.id).then(() => {
            locker.opening = false;
            this.service.error('Successfully opened locker');
        }, () => {
            locker.opening = false;
            this.service.error('Unable to open locker');
        });
    }
}
