
import { Component, Input, Output, EventEmitter, OnInit, OnChanges } from '@angular/core';

import { BaseComponent } from '../../../shared/globals/base.component';
import { AppService } from '../../../services/app.service';

import * as faker from 'faker';
import * as moment from 'moment';
import { IVisitorGroup } from '../../../services/data/visitors.service';

@Component({
    selector: 'visitor-listings',
    templateUrl: './visitor-listings.template.html',
    styleUrls: ['./visitor-listings.styles.scss']
})
export class VisitorListingsComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public date: number;
    @Input() public count: number;
    @Output() public countChange = new EventEmitter();
    @Output() public dateChange = new EventEmitter();

    public model: any = {};

    constructor(private service: AppService) {
        super();
        this.model.list = [];
        this.model.offset = 0;
        const cnt = Math.floor(Math.random() * 10 + 3);
        for (let i = 0; i < cnt; i++) {
            const time = moment().add(Math.floor(Math.random() * 8 - 1) * 15, 'm');
            this.model.list.push({
                name: `${faker.name.firstName()} ${faker.name.lastName()}`,
                image: faker.image.avatar(),
                date: time.valueOf(),
                arrival_time: time.format('h:mm a'),
            });
        }
        this.model.list.sort((a, b) => a.time - b.time);
        this.postCount();
    }

    public ngOnInit() {
        this.model.today = moment().valueOf();
        this.subscription('visitor_list', this.service.Visitors.listen('timeline', (list) => {
            this.model.visitor_list = list;
            this.updateList();
        }));
    }

    public ngOnChanges(changes: any) {
        if (changes.date) {
            const day = moment(this.date);
            this.model.date_display = moment(this.date).format('DD MMM YYYY');
            this.model.date = day.isSame(moment(), 'd') ? 'Today' : day.format('ddd, Do MMMM');
            this.updateList();
        }
    }

    public view(item) {
        this.service.Visitors.view(item);
    }

    public changeDate() {
        this.dateChange.emit(this.date);
        this.model.show_calendar = false;
    }

    public updateList() {
        const date = moment(this.date || '').format('YYYY/MM/DD');
        const v_list = this.model.visitor_list;
        const visitors = v_list ? v_list[date] || [] : [];
        const last_update = this.service.Visitors.get('last_update') || 0;
        const expiry = moment().add(-5, 'm');
        this.model.list = visitors;
        this.model.list.sort((a, b) => a.date - b.date);
        if (!this.model.list || this.model.list.length <= 0 || last_update < expiry.valueOf()) {
            const start = moment(this.date || '').hours(0).minutes(0).unix();
            const end = moment(this.date || '').hours(23).minutes(59).unix();
            // this.service.Bookings.query({ from: start, to: end });
        }
        this.postCount();
    }

    public postCount() {
        setTimeout(() => {
            this.count = this.model.list ? this.model.list.length : 0;
            this.countChange.emit(this.count);
        }, 50);
    }

    public newVisitor() {
        this.service.Visitors.new((e) => {

        });
    }

    public groupId(index: number, item: IVisitorGroup) {
        return item ? item.id : index;
    }
}
