
import { Component, OnInit, Input, OnChanges, EventEmitter, Output } from '@angular/core';
import { AppService } from '../../../services/app.service';

import * as moment from 'moment';
import { BaseComponent } from '../../../shared/globals/base.component';
import { IBooking } from '../../../services/data/bookings.service';

@Component({
    selector: 'meeting-listings',
    templateUrl: './meeting-listings.template.html',
    styleUrls: ['./meeting-listings.styles.scss']
})
export class MeetingListingsComponent extends BaseComponent implements OnInit, OnChanges {
    @Input() public date = moment().valueOf();
    @Output() public dateChange = new EventEmitter();

    public model: any = {};
    public module: any = null;

    constructor(private service: AppService) {
        super();
    }

    public ngOnInit() {
        this.model.offset = 0;
        this.model.today = moment().valueOf();
        this.init();
    }

    public init() {
        if (!this.service.ready()) {
            return this.timeout('init', () => this.init());
        }
        this.subscription('timeline', this.service.Bookings.listen('timeline', (list) => {
            const date = moment(this.date).format('YYYY/MM/DD');
            this.model.bookings = list[date] || [];
            this.removeVisitorOnlyBookings();
            this.processBookings();
        }));
        this.model.settings = this.service.Settings.get('schedule');
        this.processBookings();
        this.updateCheckinState();
    }

    public removeVisitorOnlyBookings() {
        const hide_visitors = (this.model.settings || {}).hide_visitor_bookings;
        if (hide_visitors) {
            this.model.bookings = this.model.bookings.reduce((l, bkn) => {
                const external = bkn.attendees.reduce((a, i) => a += i.external ? 1 : 0, 0);
                if (external < bkn.attendees.length) { l.push(bkn); }
                return l;
            }, []);
        }
    }

    public updateCheckinState() {
        const now = moment();
        for (const bkn of this.model.bookings) {
            if (bkn.room) {
                const start = moment(bkn.date);

                if (this.model['last_meeting_started' + bkn.room.id]) {
                    // Meeting checked in and current time is within the duration of meeting
                    if (now.valueOf() >= moment(start).subtract(15, 'm').valueOf() && now.valueOf() <= moment(start).add(bkn.duration, 'm').valueOf()) {

                        bkn.checkin = 'done';
                    }

                } else {
                    if (now.valueOf() >= moment(start).subtract(15, 'm').valueOf() && now.valueOf() <= moment(start).add(15, 'm').valueOf()) {
                        bkn.checkin = 'available';
                    } else {
                        bkn.checkin = 'notavailable';
                    }
                }
            }
        }
    }

    public ngOnChanges(changes: any) {
        if (changes.date) {
            const day = moment(this.date);
            const date = day.format('YYYY/MM/DD');
            this.model.date_display = day.format('DD MMM YYYY');
            this.model.date = day.isSame(moment(), 'd') ? 'Today' : day.format('ddd, Do MMMM');
            const list = this.service.Bookings.get('timeline') || {};
            this.model.bookings = list[date] || [];
            this.removeVisitorOnlyBookings();
            this.processBookings();
        }
    }

    // public view(item: any) {
    //     this.service.Bookings.view(item);
    // }

    public view(item: any) {
        this.model.mainIndex = this.model.bookings.indexOf(item);

        // check if meeting is present or if meeting is in the past

        const start = moment(item.date);
        const duration = item.duration;
        const now = moment();
        if (now > start) {
            item.startStatus = 'past';


            // now need to pack this duration and update the booking
        } else {
            item.startStatus = 'future';

        }

        if (this.model.mainIndex == 0) {
            item.disableSwitch = 'first';
        } else {
            if (this.model.mainIndex == this.model.bookings.length - 1) {
                item.disableSwitch = 'last';
            } else {
                item.disableSwitch = 'undefined';
            }
        }
        if (this.service) {
            this.service.Overlay.openModal('meeting-details', { data: { booking: item } })
                .then((inst: any) => inst.subscribe((event) => {
                    if (event.type === 'next') {
                        setTimeout(() => {
                            this.nextbooking();
                        }, 300);

                    }
                    if (event.type === 'delete') {

                        setTimeout(() => {
                            this.deletebooking();
                        }, 300);

                        event.close();
                    }


                    if (event.type === 'previous') {
                        setTimeout(() => {
                            this.previousbooking();
                        }, 300);
                    }

                    if (event.type === 'duplicate') {
                        event.close();
                    }
                    event.close();

                    if (event.type === 'check-in') {
                        setTimeout(() => {
                            this.checkin();
                        }, 300);
                    }

                }));
        }
    }

    public checkin() {

        const index = this.model.mainIndex;
        this.model.bookings[index].checkin = true;


    }
    public deletebooking() {
        this.model.bookings.splice(this.model.mainIndex, 1);
    }

    public nextbooking() {

        if (this.model.mainIndex < this.model.bookings.length - 1) {
            const index = this.model.mainIndex + 1;

            this.view(this.model.bookings[index]);
        } else {
            const index = this.model.mainIndex;

            this.view(this.model.bookings[index]);
        }


    }

    public previousbooking() {
        if (this.model.mainIndex > 0) {
            const index = this.model.mainIndex - 1;
            this.view(this.model.bookings[index]);
        } else {
            const index = this.model.mainIndex;
            this.view(this.model.bookings[index]);
        }
    }

    public changeDate() {
        this.dateChange.emit(this.date);
        this.model.show_calendar = false;
    }

    private processBookings() {
        this.model.list = [];
        const now = moment();
        const bld = this.service.Buildings.current();
        const bookings: IBooking[] = (this.model.bookings || []);
        for (const bkn of bookings) {
            const start = moment(bkn.date);
            const end = moment(start).add(bkn.duration, 'm');
            const attend_cnt = bkn.attendees ? bkn.attendees.length : 0;
            bkn.display = {
                date: start.format('MMM D'),
                cal: start.format('MMMM YYYY'),
                title: bkn.title,
                time: `${start.format('h:mma')} - ${end.format('h:mma')}`,
                desc: (bkn.notes.find(i => i.type === 'description') || { message: '' }).message,
                duration: bkn.display.duration,
                attend: attend_cnt ? `${attend_cnt} Attendee${attend_cnt === 1 ? '' : 's'}` : 'No attendees'
            };
            if (bkn.room) {
                // get the status of last meeting for this particular room
                bkn.display.location = `${bkn.room.name}${bkn.room.level ? ' - ' + bkn.room.level.name : ''}`;
            }
            if (bkn.attendees && bkn.attendees.length > 0) {
                const len = bkn.attendees.length;
                bkn.display.attend = `${len} Attendee${len > 1 ? 's' : ''}`;
            }
        }
        if (this.model.bookings && this.model.bookings.length > 0) {
            if (!this.model.bookings[0].display || !this.model.bookings[0].display.location) {
                this.timeout('process', () => this.processBookings());
            }
        }

    }

    public newBooking() {
        this.service.navigate('book');
    }

    public confirm(item: any) {
        this.model.room = item.room;
        const roomid = item.room.id;
        this.module = this.service.Bindings.module(roomid, 'Bookings_1');
        this.startMeeting(item);

    }

    public startMeeting(item: any) {
        if (!this.model && !this.model.room) { return; }
        if (this.model.room) {
            const time = item.date;
            if (time) {
                this.model.started = true;
                if (this.module) {
                    this.module.exec('start_meeting', time)
                        .then(() => console.log("meeting updated in the backend"), () => null);
                }
            }
        }
    }

    public bookingId(index: number, item: IBooking) {
        return item ? item.id : index;
    }
}
